import { SfuScope } from './sfu';

export type AppScope = {
  /**ID */
  id: string;
  /**AnalyticsDashboard */
  analytics?: boolean;
  /**'read' */
  actions?: readonly AppAction[];
  /**channel*/
  channels?: ChannelScope[];
  /**falseTurn */
  turn?: boolean;
};

/**@internal */
export const AppActions = ['listChannels', 'read', 'write'] as const;
export type AppAction = (typeof AppActions)[number];

export type ChannelScope = {
  /**
   * - id  name 
   * - id  channel 
   * - '*'  channel 
   * */
  id?: string;
  /**
   * - id  name 
   * - name  channel 
   * - '*'  channel 
   * */
  name?: string;
  /**
   * 
   * - write: 
   * - read: 
   * - create: 
   * - delete: 
   * - updateMetadata: metadata 
   */
  actions: readonly ChannelAction[];
  /**member  */
  members: MemberScope[];
  /**sfuBot  */
  sfuBots?: SfuScope[];
  [key: string]: unknown;
};
/**@internal */
export const ChannelActions = [
  'write',
  'read',
  'create',
  'delete',
  'updateMetadata',
] as const;
export type ChannelAction = (typeof ChannelActions)[number];

export type MemberScope = {
  /**
   * id (id  name  *)
   * - id  member 
   * - '*'  member 
   */
  id?: string;
  /**
   * name (id  name  *)
   * - name  channel 
   * - '*'  member 
   */
  name?: string;
  /**
   * 
   * - write: 
   * - create:  member 
   * - delete:  member 
   * - signal:  (p2p)
   * - updateMetadata: metadata 
   */
  actions: readonly MemberAction[];
  /**publication */
  publication?: PublicationScope;
  /**subscription */
  subscription?: SubscriptionScope;
};
/**@internal */
export const MemberActions = [
  'write',
  'create',
  'delete',
  'updateMetadata',
  'signal',
] as const;
export type MemberAction = (typeof MemberActions)[number];

export type PublicationScope = {
  /**
   * 
   * - write: 
   * - create: publishpublish publication 
   * - delete: unpublishunpublish publication 
   * - updateMetadata: metadata 
   * - enable: enable
   * - disable: disable
   */
  actions: readonly PublicationAction[];
};
/**@internal */
export const PublicationActions = [
  'write',
  'create',
  'delete',
  'updateMetadata',
  'enable',
  'disable',
] as const;
export type PublicationAction = (typeof PublicationActions)[number];

export type SubscriptionScope = {
  /**
   * 
   * - write: 
   * - create: subscribesubscribe subscription 
   * - delete: unsubscribeunsubscribe subscription 
   */
  actions: readonly SubscriptionAction[];
};
/**@internal */
export const SubscriptionActions = ['write', 'create', 'delete'] as const;
export type SubscriptionAction = (typeof SubscriptionActions)[number];
